import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {NgdsComponent} from './ngds/ngds.component';
import {StorageComponent} from './storage/storage.component';
import {VirtualizationComponent} from './virtualization/virtualization.component'; 
import {MicrosoftComponent} from './microsoft/microsoft.component';
import {AdvisoryComponent} from './advisory/advisory.component'
import {InfraComponent} from './infra/infra.component'



const routes: Routes = [
  {
    path: '',
    component:InfraComponent 
  },
  {
    path: 'Advisory1',
    component:AdvisoryComponent 
  },
  {
    path: 'Microsoft1',
    component:MicrosoftComponent 
  },
  {
    path: 'Virtualization1',
    component:VirtualizationComponent
  },
  {
    path: 'Storage1',
    component:StorageComponent
  },
  {
    path: 'Ngds',
    component:NgdsComponent
  },
]
@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
  })
  export class InfrastructureChildRoutingModule {
  }
  